    import {
    		mapState,
    		mapMutations
    	} from 'vuex';
		
        // import marked from 'marked'
        // let rendererMD = new marked.Renderer();
        //     marked.setOptions({
        //         renderer: rendererMD,
        //         gfm: true,
        //         tables: true,
        //         breaks: false,
        //         pedantic: false,
        //         sanitize: false,
        //         smartLists: true,
        //         smartypants: false
        //     });
        import uParse from '@/components/gaoyia-parse/parse.vue'
        import uniPopup from '@/components/uni-popup/uni-popup.vue'
    import apiName from '../../static/commonjs/request/apiName.js';
	export default {
        components: {
        	uniPopup,
            uParse
        },
		data() {
			return {
                loading:false,
				type: 'center',
                isApply:true,
                course_id:'',
                DateilInfo:'',
                strings:'',
                jubaoContent:''
			};
		},
        onShow() {
        	// this.getDataList()
        },
        onLoad(options) {
            var _this = this 
            let course_id = parseInt(options.course_id)
            _this.course_id = options.course_id
            console.log( _this.course_id)
        	_this.$http.post(apiName.COURSEDETAIL,{
        		params: {
        			token:_this.token,
        	        course_id:_this.course_id
        		}
        	}).then(res=>{
        	    console.log('课程详情返回数据',res)
        	    if (res.data.success) {
                    _this.DateilInfo = res.data.data
                    if(res.data.data.has_apply){
                        this.isApply = true
                    }else{
                        this.isApply = false
                    }
                    _this.strings = res.data.data.data.course_content
					// console.log('解析后的内容',_this.strings)
                    console.log('返回的内容',res.data.data.data.course_content)
                    // _this.company_info = res.data.data.company_info 
        	        console.log('成功')
        	    }else{
        	        _this.$KM.showToast('请求职位列表接口失败')
        	    }
        	}).catch(err => {
        	     console.log('失败')
        	 });
        },
        computed: {
        	...mapState(['token'])
        },
        methods: {
            preview(src, e) {
              // do something
            },
            navigate(href, e) {
              // do something
            },
            referAp(){
				if(!this.token){
				    console.log('跳转登录页')
					return uni.reLaunch({
					    url: '../login/login?iseduDetail=true&course_id=' + this.course_id
					})
				}else{
                this.$http.post(apiName.ENTER_COURSE,{
                	params: {
                		token:this.token,
                        course_id:this.course_id
                	}
                }).then(res=>{
                    if (res.data.success) {
                        console.log('成功')
                        this.$KM.showToast('咨询成功,课程老师将尽快联系你')
                        this.isApply = true
                    }else{
                        this.$KM.showToast('请求职位列表接口失败')
                    }
                }).catch(err => {
                     console.log('失败')
                 });
			 }
            },
            quxiao(){
				this.jubaoContent = ''
                this.$refs['showtip'].close()
            },
            queding(){
                if(!this.jubaoContent){
                    return uni.showToast({
                    	icon:'none',
                        title:'举报内容不能为空'
                    })
                }
                this.$http.post(apiName.REPORT_COURSE,{
                	params: {
                		token:this.token,
                        course_id:this.course_id,
                        content:this.jubaoContent
                	}
                }).then(res=>{
                    console.log(res)
                    if (res.data.success) {
                        console.log('成功')
                        this.$refs['showtip'].close()
						this.jubaoContent = ''
                         this.$KM.showToast('举报成功')
                    }else{
                        this.$KM.showToast(res.data.msg)
                    }
                }).catch(err => {
            	     console.log('失败')
            	 });
            },
            getDataList(course_id) {
                var _this = this
            	_this.$http.post(apiName.COURSEDETAIL,{
            		params: {
            			token:this.token,
                        course_id:course_id
            		}
            	}).then(res=>{
                    console.log('课程详情返回数据',res)
            	    if (res.data.success) {
            	        console.log('成功')
            	    }else{
            	        _this.$KM.showToast('请求职位列表接口失败')
            	    }
            	}).catch(err => {
            	     console.log('失败')
            	 });
            },
        	back() {
        		// uni.navigateBack({
        		// 	delta: 1
        		// })
				uni.reLaunch({
					url:'../eduTrain/eduTrain'
				})
        	},
            jubao(){
                console.log('这是token',this.token)
				// this.$refs['showtip'].open()
				// return
                if(!this.token){
                    console.log('跳转登录页')
                	uni.reLaunch({
                	    url: '../login/login?iseduDetail=true&course_id=' + this.course_id
                	})
                }else{
                    this.$refs['showtip'].open()
                }
            }
        },
	}
